import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  PanelBody,
  ModalGlobal,
  Tabel,
  showModal,
  hideModal,
  putDataNoTImeOut,
  postDataNoTImeOut,
  deleteData,
  ToastNotification,
  Swal,
  reset,
} from "../../../../components/helpers/library";
import HeadBatuFluorencence from "./HeadBatuFluorencence";
import { getDataFluorencence,ShowDataModal } from './../../../../actions/datamaster_action';

const maptostate = (state) =>{
  return {
    data : state.datamaster.getDataFluorencence
  }
}
class BatuFluorencence extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      columns: [
        {
          dataField: "kode_fluorescence",
          text: "Kode Fluorescence",
        },
        {
          dataField: "nama_fluorescence",
          text: "Nama Fluorescence",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              id: row._id,
              kode_fluorescence: row.kode_fluorescence,
              nama_fluorescence: row.nama_fluorescence,
            };
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEdit(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() => this.hapusData(row)}
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    // this.print = this.print.bind(this);
  }

  handleSubmit(data) {
    // console.log(data);
    this.setState({
      isLoading: true,
    });

    this.state.isEdit ? 
    putDataNoTImeOut('batu-fluorescence/'+data.id,data).then((res)=>{
      this.setState({
        isLoading: false,
      });
      ToastNotification('success',res.data.message || "Data Berhasil Disimpan")
      this.props.dispatch(reset())
      this.props.dispatch(getDataFluorencence())
      this.props.dispatch(hideModal());
    }).catch((err)=>{
      this.setState({
        isLoading: false,
      });
      ToastNotification('info',err.response?.data?.message || "Data Gagal Disimpan")
    })
    :
    postDataNoTImeOut('batu-fluorescence',data).then((res)=>{
      this.setState({
        isLoading: false,
      });
      ToastNotification('success',res.data.message || "Data Berhasil Disimpan")
      this.props.dispatch(reset())
      this.props.dispatch(getDataFluorencence())
      this.props.dispatch(hideModal());
    }).catch((err)=>{
      this.setState({
        isLoading: false,
      });
      ToastNotification('info',err.response?.data?.message || "Data Gagal Disimpan")
    })
  }
   

  componentDidMount() {
    this.props.dispatch(getDataFluorencence())
  }

  formEdit(data) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowDataModal(data));
    this.setState({
      isEdit: true,
    });
  }
  showModal() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowDataModal(true));
    this.setState({
      isEdit: false,
    });
  }

  hapusData(row) {
    Swal.fire({
      // title: "Anda Yakin !!",
      // text: "Ingin Menghapus Data Ini ?",
      html:
        "Apakah Anda Yakin Ingin " +
        "Menghapus " +
        "<h1><b>Kode Fluorescence " +
        row.kode_fluorescence +
        "</b> ?</h1>",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
       deleteData('batu-Fluorescence/'+row._id).then((res)=>{
         ToastNotification('success',res.data.message || "Data Berhasil Disimpan")
         this.props.dispatch(getDataFluorencence())
        }).catch((err)=>{
         ToastNotification('info',err.response?.data?.message || "Data Gagal Disimpan")
       })
      }
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item">
            <Link to="#">Data Berlian</Link>
          </li>
          <li className="breadcrumb-item active">Batu Fluorescence</li>
        </ol>
        <h1 className="page-header">Batu Fluorescence </h1>
        <Panel>
          <PanelHeader>Batu Fluorescence</PanelHeader>
          <PanelBody>
            <Tabel
              keyField="kode_fluorescence"
              tambahData={true}
              handleClick={() => this.showModal()}
              exportCsv={true}
              data={this.props.data}
              columns={this.state.columns}
              empty={this.props.data}
              textEmpty="Batu Color Kosong"
            />
          </PanelBody>
        </Panel>

        <ModalGlobal
          size="P"
          title={this.state.isEdit ? "Edit Data Color" : "Tambah Data Color"}
          content={
            <HeadBatuFluorencence
              isLoading={this.state.isLoading}
              isEdit={this.state.isEdit}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          }
        />
      </div>
    );
  }
}

BatuFluorencence = reduxForm({
  form: "BatuFluorencence",
  enableReinitialize: true,
})(BatuFluorencence);
export default connect(maptostate)(BatuFluorencence);
